@import url("https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@400;500;600;700&display=swap");
@import url("https://db.onlinewebfonts.com/c/e2b9630cd56b1778e841e805b47c9347?family=Digital+V1");
@import url("https://db.onlinewebfonts.com/c/ba883c280c4055f0ae9280fa9f380fbc?family=Digitalix");
body {
  font-family: "Chakra Petch", sans-serif;
  background-color: #2c2c54;
}

#calculator {
  width: 300px;
  box-shadow: 3px 3px 10px #222;
}
#calculator #display-wrap {
  height: 4.5rem;
  font-family: "Digital V1", monospace;
  line-height: normal;
  letter-spacing: 0.1rem;
}
#calculator #display-wrap .current-operand {
  font-size: 1.75rem;
}
#calculator #display-wrap .previous-operand {
  font-size: 1rem;
}
#calculator button {
  font-family: "Digitalix", monospace;
  font-size: 1rem;
  height: 2.5rem;
  border: solid 1px #222;
}
#calculator .calc-gif {
  height: 2.5rem;
}
#calculator #one {
  color: #333;
}
#calculator #two {
  color: #333;
}
#calculator #three {
  color: #333;
}
#calculator #four {
  color: #333;
}
#calculator #five {
  color: #333;
}
#calculator #six {
  color: #333;
}
#calculator #seven {
  color: #333;
}
#calculator #eight {
  color: #333;
}
#calculator #nine {
  color: #333;
}
#calculator #zero {
  color: #333;
}
#calculator #decimal {
  color: #333;
}
#calculator #add {
  color: #333;
}
#calculator #subtract {
  color: #333;
}
#calculator #multiply {
  color: #333;
}
#calculator #divide {
  color: #333;
}
#calculator #clear {
  font-size: 0.8rem !important;
  padding-top: 0.625rem !important;
}
#calculator #equals {
  font-size: 0.8rem !important;
  padding-top: 0.625rem !important;
}

footer {
  font-size: 0.75rem;
}
footer .credit {
  font-size: 0.625rem;
}
footer a {
  color: #14ff99;
  text-decoration: none;
  font-weight: 500;
}
footer a:hover, footer a:focus-visible {
  color: #0fc9ed;
  text-decoration: #14ff99 underline;
  text-decoration-thickness: 0.11rem;
  text-underline-offset: 0.1rem;
}
footer a:active {
  color: #47ffaf;
}

.row {
  margin-top: 2px !important;
}